import React from 'react';
import PropTypes from 'prop-types';
const functions = require('../../resources/functions.json');

const schemas = {
  "rr": "http://www.w3.org/ns/r2rml#",
  "rml": "http://semweb.mmlab.be/ns/rml#",
  "fno": "http://w3id.org/function/ontology#",
  "fnml": "http://semweb.mmlab.be/ns/fnml#",
  "dbf": "http://dbpedia.org/function/"
};

let fnParameters = {};
functions.forEach(fn => {
  fnParameters[fn.url] = {};
  fn.parameters.forEach(param => {
    fnParameters[fn.url][param.url] = param;
  });
});

const FunctionEditor = ({pom, onChange}) => {
  let executesPom = pom['rr:objectMap']['fnml:functionValue']['rr:predicateObjectMap'].filter(fnPom => {
    return fnPom['rr:predicate']['@id'] === 'fno:executes';
  })[0];
  let fnConfig = functions.filter(fn => {
    return deprefix(executesPom['rr:objectMap']['rr:constant']['@id']) === fn.url;
  })[0];
  let paramPoms = [];
  let newPoms = [];
  Object.keys(fnParameters[fnConfig.url]).forEach(fnParamUrl => {
    let paramPom = pom['rr:objectMap']['fnml:functionValue']['rr:predicateObjectMap'].filter(fnPom => {
      return deprefix(fnPom['rr:predicate']['@id']) === fnParamUrl;
    })[0];
    if (!paramPom) {
      paramPom = {
        "rr:predicate": {
          "@id": fnParamUrl
        },
        "rr:objectMap": {
          "@type": "rr:ObjectMap",
        }
      };
      if (fnParameters[fnConfig.url][fnParamUrl].ref) {
        paramPom['rr:objectMap']['rml:reference'] = {'@value': '[edit value]'};
      }
      else {
        paramPom['rr:objectMap']['rr:constant'] = {'@value': '[edit value]'};
      }
      newPoms.push(paramPom);
    }
    paramPoms.push({
      config: fnParameters[fnConfig.url][fnParamUrl],
      pom: paramPom
    });
  });

  if (newPoms.length > 0) {
    pom['rr:objectMap']['fnml:functionValue']['rr:predicateObjectMap'] = pom['rr:objectMap']['fnml:functionValue']['rr:predicateObjectMap'].concat(newPoms);
  }

  return (<div>
    <div className="input-group">
      <span className="input-group-addon">Function</span>
      <select className="form-control" value={executesPom['rr:objectMap']['rr:constant']['@id']} onChange={(e) => {
        executesPom['rr:objectMap']['rr:constant']['@id'] = e.target.value;
        onChange(pom);
      }}>
        {functions.map(fn => {
          return (<option key={fn.url} value={fn.url}>{fn.name}</option>);
        })}
      </select>
    </div>
    {paramPoms.map(fnPom => {
      let value = fnPom.config.ref ? fnPom.pom['rr:objectMap']['rml:reference']['@value'] : fnPom.pom['rr:objectMap']['rr:constant']['@value'];
      return (<div key={fnPom.config.url} className="input-group">
        <span className="input-group-addon">Parameter (<em>{fnPom.config.name}</em>)</span>
        {fnPom.config.options ? (<select className="form-control" value={value} onChange={(e) => {
          if (fnPom.config.ref) {
            fnPom.pom['rr:objectMap']['rml:reference']['@value'] = e.target.value;
          }
          else {
            fnPom.pom['rr:objectMap']['rr:constant']['@value'] = e.target.value;
          }
          onChange(pom);
        }}>
          {fnPom.config.options.map(option => {
            return <option key={option.key} value={option.key}>{option.value}</option>;
          })}
        </select>) : (<input className="form-control" type="text" value={value} onChange={(e) => {
          if (fnPom.config.ref) {
            fnPom.pom['rr:objectMap']['rml:reference']['@value'] = e.target.value;
          }
          else {
            fnPom.pom['rr:objectMap']['rr:constant']['@value'] = e.target.value;
          }
          onChange(pom);
        }}/>)}
      </div>);
    })}
  </div>);
};

FunctionEditor.propTypes = {
  pom: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired
};

function deprefix(url) {
  Object.keys(schemas).forEach(prefix => {
    url = url.replace(new RegExp(prefix + ':', 'g'), schemas[prefix]);
  });
  return url;
}

export default FunctionEditor;



