import React from 'react';
import PropTypes from 'prop-types';

const WikiPageInput = ({wikiPages, currentWikiPage, onChange}) => {
  const typeToLabel = {
    country: 'Country'
  };
  return (
    <div className="form-group">
      <select className="form-control" value={currentWikiPage} onChange={(e) => {
        onChange(e.target.value);
      }}>
        {
          wikiPages.order.map(id => {
            let page = wikiPages.pages[id];
            return <option key={id} value={id}>{page.name} <em>{typeToLabel[page.type]}</em></option>;
          })
        }
      </select>
    </div>
  );
};

WikiPageInput.propTypes = {
  wikiPages: PropTypes.object.isRequired,
  currentWikiPage: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired
};

export default WikiPageInput;



