import React from 'react';
import PropTypes from 'prop-types';
import FunctionEditor from "./FunctionEditor";

const Editor = ({mapping, onChange}) => {
  return (
    <div>
      <ul className="nav nav-tabs" role="tablist">
        <li role="presentation" className="active"><a href="#tab-input-editor" aria-controls="home" role="tab" data-toggle="tab">Editor</a></li>
        <li role="presentation"><a href="#tab-input-json" aria-controls="home" role="tab" data-toggle="tab">JSON</a></li>
      </ul>
      <div className="tab-content">
        <div role="tabpanel" className="tab-pane active" id="tab-input-editor">
          <div>
            <div className="form-group">
              <label className="floating-label-form-group">URI</label>
              <input type="text" className="form-control" value={mapping['dbf:doc']['rr:subjectMap']['rr:constant']['@value']} onChange={(e) => {
                mapping['dbf:doc']['rr:subjectMap']['rr:constant']['@value'] = e.target.value;
                onChange(mapping);
              }}/>
            </div>
            <hr/>
            {mapping['dbf:doc']['rr:predicateObjectMap'].map((pom, index) => {
              return (<div key={index}>
                <div className="input-group">
                  <span className="input-group-addon">Predicate</span>
                  <input className="form-control" type="text" value={pom['rr:predicate']['@id']} onChange={(e) => {
                    pom['rr:predicate']['@id'] = e.target.value;
                    onChange(mapping);
                  }}/>
                </div>
                <FunctionEditor pom={pom} onChange={(newPom) => {
                  pom = newPom;
                  onChange(mapping);
                }}/>
                <hr/>
              </div>);
            })}
          </div>
        </div>
        <div role="tabpanel" className="tab-pane" id="tab-input-json">
                <textarea className="form-control" style={{height: 400}} value={JSON.stringify(mapping, null, '  ')} onChange={(e) => {
                  let newMapping = null;
                  try {
                    newMapping = JSON.parse(e.target.value);
                  } catch (err) {
                    console.log('JSON not valid!');
                  }
                  if (newMapping) {
                    onChange(newMapping);
                  }
                }}/>
        </div>
      </div>
    </div>);
};

Editor.propTypes = {
  mapping: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired
};

export default Editor;



