/* global NProgress */
import React from 'react';
import * as $ from 'jquery';
import WikiPageInput from "./WikiPageInput";
import Editor from "./Editor";
import config from '../../config';
const countryMapping = require('../../resources/countryMapping.json');

const LOADING_STATE = {
  INIT: 'INIT',
  LOADING: 'LOADING',
  LOADED: 'LOADED',
  ERROR: 'ERROR',
};

const mappingDefaults = {
  country: countryMapping
};

class HomePage extends React.Component {
  constructor(props, context) {
    super(props, context);

    let wikiPages = {
      order: ['United_States', 'Belgium'],
      pages: {
        'Belgium': {name: 'Belgium', type: 'country'},
        'United_States': {name: 'United States', type: 'country'}
      }
    };

    let mappings = {};
    wikiPages.order.forEach(id => {
      let type = wikiPages.pages[id].type;
      mappings[id] = JSON.parse(JSON.stringify(mappingDefaults[type]));
    });

    this.state = {
      wikiPages,
      currentWikiPage: 'Belgium',
      mappings,
      state: LOADING_STATE.INIT
    };

    this.generate = this.generate.bind(this);
    this.change = this.change.bind(this);
    this.changeMap = this.changeMap.bind(this);
  }

  generate() {
    let {mappings, currentWikiPage} = this.state;
    let map = mappings[currentWikiPage];
    let wiki = currentWikiPage;
    let self = this;
    NProgress.start();
    this.setState({state: LOADING_STATE.LOADING});
    $.ajax({
      type: "POST",
      url: `${config.endpoint}_dbpedia`,
      data: JSON.stringify({map, wiki}),
      headers: {'Content-Type': 'text/plain'},
      success: function (data, status, jqXHR) {
        if (jqXHR.status !== 200) {
          self.setState({state: LOADING_STATE.ERROR});
        }
        else {
          self.setState({state: LOADING_STATE.LOADED, lastAnswer: data});
        }
        NProgress.done();
      },
      error: function () {
        self.setState({state: LOADING_STATE.ERROR});
        NProgress.done();
      }
    });
  }

  change(id) {
    this.setState({currentWikiPage: id});
  }

  changeMap(newMap) {
    let {mappings, currentWikiPage} = this.state;
    mappings[currentWikiPage] = newMap;
    this.setState({mappings});
  }

  render() {
    let {generate, change, changeMap} = this;
    let {mappings, currentWikiPage, state, lastAnswer, wikiPages} = this.state;

    let output = null;
    switch (state) {
      case LOADING_STATE.ERROR:
        output = <pre><code>Something went wrong, please try again, or refresh the page</code></pre>;
        break;
      case LOADING_STATE.LOADING:
        output = <pre><code>Fetching the content, please hold on...</code></pre>;
        break;
      case LOADING_STATE.LOADED:
        output = <pre><code>{lastAnswer.triples}</code></pre>;
        break;
    }

    return (
      <div>
        <div className="row">
          <div className="col-sm-12">
            <p style={{textAlign: 'center'}}>
              Let's
              <button className="btn btn-primary" onClick={generate}>generate</button>
              some DBpedia data!
            </p>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <WikiPageInput wikiPages={wikiPages} currentWikiPage={currentWikiPage} onChange={change}/>
          </div>
          <div className="col-sm-1">
            <a onClick={() => {
              mappings[currentWikiPage] = JSON.parse(JSON.stringify(mappingDefaults[wikiPages.pages[currentWikiPage].type]));
              this.setState({mappings});
            }}>reset</a>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6" style={{zIndex: 9999}}>
            <Editor mapping={mappings[currentWikiPage]} onChange={changeMap}/>
          </div>
          <div className="col-sm-6" id="output">
            <div>
              <ul className="nav nav-tabs" role="tablist">
                <li role="presentation" className="active"><a href="#tab-orig-output" aria-controls="home" role="tab" data-toggle="tab">Output</a></li>
                <li role="presentation"><a href="#tab-mapping" aria-controls="home" role="tab" data-toggle="tab">Mapping</a></li>
              </ul>
              <div className="tab-content">
                <div role="tabpanel" className="tab-pane active" id="tab-orig-output">{output}</div>
                <div role="tabpanel" className="tab-pane" id="tab-mapping">{state === LOADING_STATE.LOADED ?
                  <pre><code>{lastAnswer.mapping}</code></pre> : null}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default HomePage;



